<template>
  <div ref="line_ref" class="line-home"></div>
</template>

<script>
export default {
  data(){
    return{
      chartInstance:null,
      saleData:[]
    }
  },
  mounted() {
    this.updateChart();
    this.saleData=[
      [
        {address:'',sales:'251',upYoy:'2.1%',upChain:'10%'},
        {address:'',sales:'239',upYoy:'11%',upChain:' 12%'},
        {address:'',sales:'269',upYoy:'2.8%',upChain:' 8%'},
        {address:'',sales:'289',upYoy:'3.6%',upChain:'11%'},
      ],
      [
        {address:'',sales:'260',upYoy:'2.3%',upChain:'8%'},
        {address:'',sales:'209',upYoy:'10%',upChain:'15%'},
        {address:'',sales:'359',upYoy:'4.1%',upChain:'12%'},
        {address:'',sales:'389',upYoy:'5.1%',upChain:'16%'},
      ],
      [
        {address:'',sales:'201',upYoy:'1.5%',upChain:'8%'},
        {address:'',sales:'199',upYoy:'10%',upChain:'13%'},
        {address:'',sales:'369',upYoy:'4.2%',upChain:'13%'},
        {address:'',sales:'289',upYoy:'3.6%',upChain:'9%'},
      ],
      [
        {address:'',sales:'241',upYoy:'3.1%',upChain:'11%'},
        {address:'',sales:'339',upYoy:'13%',upChain:'13%'},
        {address:'',sales:'469',upYoy:'5%',upChain:'18%'},
        {address:'',sales:'279',upYoy:'3.5%',upChain:'8%'},
      ]
    ]
  },
  methods: {
    updateChart() {
      this.chartInstance = this.$echarts.init(this.$refs.line_ref);
      const option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          textStyle: {
            color: '#fff',
          },
          top:10,
          data: ["", ""],
        },
        grid: {
          top: "24%",
          left: "4%",
          right: "6%",
          bottom: "5%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLabel: {
              textStyle: {
                color: "#FFF"
              },
              fontSize:10
            },
            data: ["1", "2", "3", "4"],
          },
        ],
        yAxis: [
          {
            type: "value",
            offset:5 ,
            axisTick: {
              show: false,  //
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              textStyle: {
                color: "#FFF",
                fontSize:10
              }
            }
          },
        ],
        series: [
          {
            name: "",
            type: "line",
            showAllSymbol: true,
            symbol: 'circle',
            symbolSize: 5,
            lineStyle: {
              color: '#e8dd00'
            },
            label: {
              show: true,
              position: 'top',
              textStyle: {
                color: '#fff',
                fontSize:10
              }
            },
            itemStyle: {
              color: '#e8dd00',
              borderColor: '#e8dd00',
              borderWidth: 2
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                    {
                      offset: 0,
                      color: 'rgba(255, 218, 0, 0.3)',
                    },{
                      offset: 1,
                      color: 'rgba(255, 218, 0, 0)',
                    },
                  ],
                  false
                ),
              },
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134],
          },
          {
            name: "",
            type: "line",
            showAllSymbol: true,
            symbol: 'circle',
            symbolSize: 5,
            lineStyle: {
              color: '#09b91a'
            },
            label: {
              show: true,
              position: 'top',
              textStyle: {
                color: '#fff',
                fontSize:10
              }
            },
            itemStyle: {
              color: '#09b91a',
              borderColor: '#09b91a',
              borderWidth: 2
            },
            areaStyle: {
               normal: {
                  color: new this.$echarts.graphic.LinearGradient(
                    0, 0,  0, 1,
                    [
                      {
                        offset: 0,
                        color: 'rgba(12,255,0,0.3)',
                      },{
                        offset: 1,
                        color: 'rgba(12,255,0,0)',
                      },
                    ],
                    false
                  ),
                },
            },
            emphasis: {
              focus: "series",
            },
            data: [220, 182, 191, 234],
          }
        ],
      };
      this.chartInstance.setOption(option);
      var that = this
      that.chartInstance.getZr().off('click');
      that.chartInstance.getZr().on('click', function (params) {
        const pointInPixel = [params.offsetX, params.offsetY]
        // 
        if ( that.chartInstance.containPixel('grid', pointInPixel)) {
          //  x   
          const xIndex =  that.chartInstance.convertFromPixel({ seriesIndex: 0 }, pointInPixel)[0]
          that.openData(xIndex)
        }
      });
    },
    openData(xIndex){
			this.$parent.clickDialog(this.saleData,'dingdan',``,xIndex)
		}
  },
};
</script>

<style lang='scss'>
.line-home{
    width: 100%;
    height: 100%;
}
</style>