<template>
	<div id="cloudWords" @click="openData"></div>
</template>
<script>
import CloudWords from './wordCloud/index'
export default {
  data() {
    return {
			cloudWords: null,
			words : ['', '', '', '', '', '', '', '', '', '', '', ''],
 			colors:['#68A2FF','#0CDFEF','#78EA7E','#FFDA00','#D0DEEE','#6EC0FF','#47B0FF','#A48BEB','#FFDA00','#939FB0', '#FFC97A', '#7BD280'],
			wordsData:[]
    };
  },
  mounted() {
		this.getWindowInfo()
		window.addEventListener('resize', this.getWindowInfo);
		this.wordsData = [
			{name:'',address:'',sales:'5623',sortNum:'0'},
			{name:'',address:'',sales:'5341',sortNum:'0'},
			{name:'',address:'',sales:'2875',sortNum:'1'},
			{name:'',address:'',sales:'2183',sortNum:'0'},
			{name:'',address:'',sales:'2001',sortNum:'1'},
			{name:'',address:'',sales:'1872',sortNum:'0'},
			{name:'',address:'',sales:'1287',sortNum:'0'},
		]
	},
	destroyed() {
    clearInterval(this.timerId);
    // 
    window.removeEventListener("resize", this.getWindowInfo);
  },
  methods: {
		init(){
			this.cloudWords = new CloudWords({
				el: '#cloudWords',
				words: this.words,
				colors: this.colors,
				minSize: 14,
				maxSize: 40,
				onClick (elem) {
					console.log(elem)
				}
			})
		},
		getWindowInfo(){
			const windowInfo = {
				width: window.innerWidth,
				hight: window.innerHeight
			}
			this.cloudWords = null
			document.getElementById('cloudWords').innerHTML = ''
			this.init()
		},
		openData(){
			this.$parent.clickDialog(this.wordsData,'ciyun','')
		}
	},
};
</script>

<style lang="less" scode>
	#cloudWords {
		width: 90%;
		height: 90%;
		margin: 0 auto;
	}
</style>