<template>
<!--  -->
  <div class="columnar-home" id="seller_ref" ref="seller_ref"></div>
</template>

<script>
// import chalk from "../../../map/chalk.js";
// import dark from "../../../map/dark";
// import westeros from "../../../map/westeros";
export default {
  data() {
    return {
      a: "westeros",
      b: "dark",
      chartInstance: null,
      // 
      allData: null,
      currentPage: 1, //
      totalPage: 0, //
      timerId: null, //
      option:null
    };
  },
  mounted() {
    this.getData();
  },
  //   
  destroyed() {
    clearInterval(this.timerId);
  },
  methods: {
    // 
    getData() {
      this.allData = [
        { name: "", value: 120 },
        { name: "", value: 176 },
        { name: "", value: 90 },
        { name: "", value: 100 },
        { name: "", value: 70 },
        { name: "", value: 50 },
        { name: "", value: 170 },
        { name: "", value: 70 },
        { name: "", value: 30 },
        { name: "", value: 90 },
        { name: "", value: 50 },
        { name: "", value: 200 },
        { name: "", value: 55 },
        { name: "", value: 130 },
        { name: "", value: 25 },
        { name: "", value: 70 },
        { name: "", value: 140 },
        { name: "", value: 160 },
        { name: "", value: 50 },
        { name: "", value: 20 },
        { name: "", value: 10 },
        { name: "", value: 170 },
        { name: "", value: 40 },
        { name: "", value: 50 },
        { name: "", value: 60 },
        { name: "", value: 50 },
        { name: "", value: 80 },
        { name: "", value: 90 },
        { name: "", value: 100 },
        { name: "", value: 110 },
      ];
      //   
      this.allData.sort((a, b) => {
        return a.value - b.value; //
      });
      // 5
      if( this.allData.length){
        this.totalPage = this.allData.length % 10 === 0? this.allData.length / 10 : Math.trunc(this.allData.length/ 10)  + 1;
      }
      //   
      this.updateChart();
      // this.startInterval()
    },
    // 
    updateChart() {
      this.chartInstance = this.$echarts.init(this.$refs.seller_ref);
      const showData = this.allData.slice(0, 10);
      this.currentPage++
      const sellerNames = showData.map((item) => {
        return item.name;
      });
      const sellerValues = showData.map((item) => {
        return item.value;
      });
      let that = this
      that.option = {
        // 
        grid: {
          top: 10,
          left: "3%",
          right: "17%",
          bottom: "1%",
          containLabel: true, //
        },
        xAxis: {
          type: "value",
          // x
          show: false
        },
        yAxis: {
          type: "category",
          data: sellerNames,
          max: 9,
          splitNumber: 0.5,
          // yshow false
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        },
        series: [
          {
            type: "bar",
            label: {
              show: true,
              position: "right",
              textStyle: {
                color: "white",
              },
              // formattera,b,c
              formatter: "{c}"
            },
            data: sellerValues,
            itemStyle: {
              normal: {
                barBorderRadius: [0, 60, 60, 0],
                color: function (params) {
                  // params->
                  if (params.dataIndex < 7) {
                    return new that.$echarts.graphic.LinearGradient(0, 0, 1, 0, [
                      {
                        offset: 0,
                        color: "rgb(2, 83, 246, 1)",
                      },
                      {
                        offset: 1,
                        color: "rgb(20, 149, 251, 1)",
                      },
                    ])
                  } else {
                    return new that.$echarts.graphic.LinearGradient(0, 0, 1, 0, [
                      {
                        offset: 0,
                        color: "rgb(15, 255, 5, 1)",
                      },
                      {
                        offset: 1,
                        color: "rgb(131, 253, 226, 1)",
                      },
                    ])
                  }
                },
              }
            },
            barWidth:6
          },
        ],
      };
      //   
      that.chartInstance.off('click');
      // 
      that.chartInstance.on("mouseover", function(params)  {
        clearInterval(that.timerId);        
				that.timer = null;
      });
      // 
      that.chartInstance.on("mouseout", function(params) {
        that.startInterval()
      });
      that.chartInstance.clear();//
      that.chartInstance.setOption(that.option);
      //   
      this.startInterval();
    },
    // 
    startInterval() {
      if (this.timerId) {
        clearInterval(this.timerId);
        this.timerId = null
      }
      var that = this
      that.timerId = setInterval(function(params) {
        const start = (that.currentPage - 1) * 10;
        const end = that.currentPage * 10;
        const showData = that.allData.slice(start, end);
        console.log(that.currentPage)
        // console.log(showData)
        const sellerNames = showData.map((item) => {
          return item.name;
        });
        const sellerValues = showData.map((item) => {
          return item.value;
        });
        that.option.yAxis.data = sellerNames
        that.option.series[0].data = sellerValues
        that.currentPage++;
        if (that.currentPage > that.totalPage) {
          that.currentPage = 1;
        }
        that.chartInstance.clear();//
        that.chartInstance.setOption(that.option);
      }, 3000);
    },
  },
};
</script>

<style lang="scss">
.columnar-home {
  width: 100%;
  height: 100%;
}
</style>
